"use client";

import { useState, useEffect } from "react";
import Link from "next/link";
import Image from "next/image";
import { useAuth } from "@/context/AuthContext";

// Valid status values - must match the model
const VALID_STATUSES = {
  PENDING: "pending",
  RESOLVED: "resolved",
  ARCHIVED: "archived",
  CANCELLED: "cancelled",
  REJECTED: "rejected",
  APPROVED: "approved",
};

export default function EnquiryManagement() {
  const { user } = useAuth();
  const [enquiries, setEnquiries] = useState({ received: [], sent: [] });
  const [activeTab, setActiveTab] = useState("received");
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    if (user) {
      fetchEnquiries();
    }
  }, [user]);

  const fetchEnquiries = async () => {
    try {
      const token = localStorage.getItem("token");
      const response = await fetch("/api/enquiries/my-enquiries", {
        headers: {
          Authorization: `Bearer ${token}`,
          "Content-Type": "application/json",
        },
      });
      if (response.ok) {
        const data = await response.json();
        setEnquiries(data);
      }
    } catch (error) {
      console.error("Failed to fetch enquiries:", error);
    } finally {
      setIsLoading(false);
    }
  };

  const handleStatusUpdate = async (enquiryId, newStatus) => {
    try {
      const token = localStorage.getItem("token");
      const response = await fetch(`/api/enquiries/${enquiryId}/status`, {
        method: "PATCH",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${token}`,
        },
        body: JSON.stringify({ status: newStatus }),
      });

      const data = await response.json();

      if (!response.ok) {
        throw new Error(data.error || "Failed to update status");
      }

      // Refresh enquiries after successful update
      await fetchEnquiries();
    } catch (error) {
      console.error("Failed to update enquiry status:", error);
      // You could add a toast notification here
    }
  };

  // Status display helper
  const getStatusDisplay = (status) => {
    return status.charAt(0).toUpperCase() + status.slice(1);
  };

  // Status color helper
  const getStatusColor = (status) => {
    switch (status) {
      case VALID_STATUSES.PENDING:
        return "bg-yellow-100 text-yellow-800";
      case VALID_STATUSES.CONTACTED:
        return "bg-blue-100 text-blue-800";
      case VALID_STATUSES.RESOLVED:
        return "bg-green-100 text-green-800";
      case VALID_STATUSES.ARCHIVED:
        return "bg-gray-100 text-gray-800";
      default:
        return "bg-gray-100 text-gray-800";
    }
  };

  if (isLoading) {
    return <div>Loading...</div>;
  }

  const displayEnquiries =
    activeTab === "received" ? enquiries.received : enquiries.sent;

  return (
    <div className="container mx-auto px-4 py-8">
      <div className="flex justify-between items-center mb-6">
        <h2 className="text-2xl font-bold">Enquiries Management</h2>
        <div className="flex space-x-4">
          <button
            onClick={() => setActiveTab("received")}
            className={`px-4 py-2 rounded ${
              activeTab === "received"
                ? "bg-blue-500 text-white"
                : "bg-gray-200 text-gray-700"
            }`}
          >
            Received ({enquiries.received?.length || 0})
          </button>
          <button
            onClick={() => setActiveTab("sent")}
            className={`px-4 py-2 rounded ${
              activeTab === "sent"
                ? "bg-blue-500 text-white"
                : "bg-gray-200 text-gray-700"
            }`}
          >
            Sent ({enquiries.sent?.length || 0})
          </button>
        </div>
      </div>

      <div className="space-y-4">
        {displayEnquiries?.length > 0 ? (
          displayEnquiries.map((enquiry) => (
            <div
              key={enquiry._id}
              className="bg-white rounded-lg shadow-md p-6"
            >
              <div className="flex items-start gap-6">
                <div className="w-24 h-24 relative flex-shrink-0">
                  <Image
                    src={enquiry.property?.images?.[0] || "/placeholder.jpg"}
                    alt={enquiry.property?.title || "Property"}
                    fill
                    className="object-cover rounded-lg"
                  />
                </div>

                <div className="flex-grow">
                  <div className="flex justify-between items-start mb-4">
                    <div>
                      <Link
                        href={`/properties/${enquiry.property?._id}`}
                        className="text-xl font-semibold hover:text-blue-600"
                      >
                        {enquiry.property?.title}
                      </Link>
                      <p className="text-sm text-gray-500">
                        {new Date(enquiry.createdAt).toLocaleDateString(
                          "en-US",
                          {
                            year: "numeric",
                            month: "long",
                            day: "numeric",
                            hour: "2-digit",
                            minute: "2-digit",
                          }
                        )}
                      </p>
                    </div>
                    {activeTab === "received" && (
                      <select
                        value={enquiry.status}
                        onChange={(e) =>
                          handleStatusUpdate(enquiry._id, e.target.value)
                        }
                        className="border rounded px-2 py-1 text-sm bg-white"
                      >
                        <option value={VALID_STATUSES.PENDING}>Pending</option>
                        <option value={VALID_STATUSES.CONTACTED}>
                          Contacted
                        </option>
                        <option value={VALID_STATUSES.RESOLVED}>
                          Resolved
                        </option>
                        <option value={VALID_STATUSES.ARCHIVED}>
                          Archived
                        </option>
                      </select>
                    )}
                  </div>

                  <div className="space-y-2 mb-4">
                    <p className="text-gray-700">
                      <span className="font-semibold">From: </span>
                      {activeTab === "received"
                        ? enquiry.sender?.name
                        : user?.name}
                    </p>
                    <p className="text-gray-700">
                      <span className="font-semibold">Email: </span>
                      {activeTab === "received"
                        ? enquiry.sender?.email
                        : user?.email}
                    </p>
                    <p className="text-gray-700">
                      <span className="font-semibold">Message: </span>
                      {enquiry.message}
                    </p>
                  </div>

                  <div className="flex justify-between items-center text-sm text-gray-500">
                    {activeTab === "received" ? (
                      <Link
                        href={`mailto:${enquiry.sender?.email}`}
                        className="text-blue-500 hover:text-blue-600"
                      >
                        Reply via Email
                      </Link>
                    ) : (
                      <div>
                        <span>Sent to: </span>
                        <span className="font-medium">
                          {enquiry.recipient?.name}
                        </span>
                      </div>
                    )}
                    <span
                      className={`px-3 py-1 rounded-full text-sm ${getStatusColor(
                        enquiry.status
                      )}`}
                    >
                      {getStatusDisplay(enquiry.status)}
                    </span>
                  </div>
                </div>
              </div>
            </div>
          ))
        ) : (
          <div className="text-center py-8 text-gray-500">
            No {activeTab} enquiries found.
          </div>
        )}
      </div>
    </div>
  );
}
