'use client';

import { useState, useEffect } from 'react';
import Link from 'next/link';
import { useRouter } from 'next/navigation';
import { useSession } from 'next-auth/react';
import { useAuth } from '@/context/AuthContext';

export default function ListingManagement() {
  const [listings, setListings] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);
  const router = useRouter();
  const { user } = useAuth();

  useEffect(() => {
    if (user) {
      fetchListings();
    }
  }, [user]);

  const fetchListings = async () => {
    try {
      setIsLoading(true);
      setError(null);

      const response = await fetch('/api/properties/my-listings', {
        credentials: 'include'
      });
      
      const data = await response.json();
      
      if (!response.ok) {
        if (response.status === 401) {
          router.push('/login');
          return;
        }
        throw new Error(data.error || 'Failed to fetch listings');
      }

      console.log('Fetched listings:', data);
      setListings(data.properties || []);
    } catch (error) {
      console.error('Failed to fetch listings:', error);
      setError(error.message || 'Failed to load listings. Please try again later.');
    } finally {
      setIsLoading(false);
    }
  };

  const handleDelete = async (listingId) => {
    if (!confirm('Are you sure you want to delete this listing?')) return;

    try {
      const response = await fetch(`/api/properties/${listingId}`, {
        method: 'DELETE',
        credentials: 'include'
      });

      if (!response.ok) {
        if (response.status === 401) {
          router.push('/login');
          return;
        }
        const data = await response.json();
        throw new Error(data.error || 'Failed to delete listing');
      }

      // Refresh listings after successful deletion
      fetchListings();
    } catch (error) {
      console.error('Failed to delete listing:', error);
      alert(error.message || 'Failed to delete listing. Please try again later.');
    }
  };

  if (isLoading) {
    return <div className="text-center py-4">Loading your listings...</div>;
  }

  if (error) {
    return (
      <div className="text-center py-4 text-red-500">
        {error}
        <button 
          onClick={fetchListings}
          className="ml-4 text-blue-500 hover:text-blue-700"
        >
          Try Again
        </button>
      </div>
    );
  }

  if (!listings.length) {
    return (
      <div className="text-center py-4">
        <p>You haven't created any listings yet.</p>
        <Link 
          href="/properties/add" 
          className="text-blue-500 hover:text-blue-700 mt-2 inline-block"
        >
          Create Your First Listing
        </Link>
      </div>
    );
  }

  return (
    <div className="space-y-4">
      {listings.map((listing) => (
        <div 
          key={listing._id} 
          className="border rounded-lg p-4 flex justify-between items-center bg-white shadow-sm"
        >
          <div>
            <h3 className="font-semibold">{listing.title}</h3>
            <p className="text-gray-600">{listing.location}</p>
            <p className="text-sm text-gray-500">
              Status: <span className="font-medium">{listing.status}</span>
            </p>
          </div>
          <div className="flex space-x-2">
            <Link
              href={`/properties/${listing._id}`}
              className="px-3 py-1 text-blue-600 hover:text-blue-800"
            >
              View
            </Link>
            <Link
              href={`/properties/edit/${listing._id}`}
              className="px-3 py-1 text-green-600 hover:text-green-800"
            >
              Edit
            </Link>
            <button
              onClick={() => handleDelete(listing._id)}
              className="px-3 py-1 text-red-600 hover:text-red-800"
            >
              Delete
            </button>
          </div>
        </div>
      ))}
    </div>
  );
}
